
body {
  margin: 0;
  padding: 0;
  overflow: hidden;
}
nav {
  box-shadow: none;
  background-color: #ea80fc;
}
.contained {
  background-color: #9FA5B5;
  display: flex;
  height: 100vh;
  width: 100%;
  overflow: hidden;
}
.error {
  margin-left: 15px;
}
.poll {
  width: 550px;
  margin: 125px auto auto auto;
  background-color: white;
  border-radius: 10px;
  overflow: visible;
  position: relative;
}

.poll h4 {
  margin-left: 15px;
}

.brand-logo {
  margin-left: 15px;
}

input ::-webkit-input-placeholder {
  color: black;
  opacity: .85;
}

input ::-ms-input-placeholder {
  color: black;
  opacity: .85;
}

input ::placeholder {
  color: black;
  opacity: .85;
}

.complete {
  height: 250px;
}
.poll i {
  color: #7FBF7F;
  margin: auto;
  font-size: 105px;
}
.thevotes {
  display: flex;
  margin-left: 15px;
  border-bottom: 1px solid #eee;
}
.votes {
  margin-left: auto;
  margin-right: 20px;
}
.preloader-wrapper {
  position: absolute;
  left: 50%;
  margin-left: -50px;
  top: 50%;
  margin-top: -50px;
}


.redder {
  border-bottom: 1px solid red !important;
}
.pollquest {
  margin: 0 0 20px 20px;
}
.row {
  display: flex;
}
.input-field {
 margin: 0 auto 0 auto;
}
.dropper {
  margin-right: 25px;
}
.drop-menu {
  overflow: hidden;
  width: 100%;
}
.dropper-btn {
  background-color: #7A97CC;
  box-shadow: none;
}
.dropper-btn:hover {
  background-color:#55698E;
}
.dropper li {
  color: blue;
}
.dropdown-content * {
  color: black !important;
}
.resp-buttons {
  display: flex;
  order: 1;
  flex-wrap: wrap;
}
.share { 
  position: relative;
  overflow: 1000;
  margin: 0 35px 0 auto;
}
.copy {
  margin: 0 25px 0 auto;
}
.share btn {
  position: absolute;
  right: 0;
}
.dropdown-content * {
  z-index: 1000;
}
.results {
  width: 100%;
}
.maindiv {
  display: flex;
  flex-direction: column;
}
.one {
  order: 0;
  margin-bottom: 45px;
}
.two {
  margin: auto 0 15px 15px;
  order: 1;
}
.flexer {
  display: flex;
  height: 250px;
  margin: auto;
}
i {
  margin: auto;
}
@media (max-width: 550px) {
  .poll {
    width: 100%;
    height: calc(100vh - 55px);
    margin: 0;
    border-radius: 0;
  }
  .sharer {
    margin-left: auto !important;
  }
.one {
  order: 1;
  margin-top: 35px;
}
  .two {
    order: 0;
    margin: 5px 5px 5px 5px;
  }
  .copy {
    margin: 0;
  }
  .rc-anchor {
    display: none;
  }
  .grecaptcha-badge {
    display: none;
  }
  .share span button {
    width: 100%;
  }
}
